	module.exports = {"data":{"markdownRemark":{"html":"<p><strong>Deadlocks</strong> can occur only in Concurrent (multi-threaded) programs where threads synchronize (use locks) access to one or more shared resources (variables and object) or instruction-set (critical-section).</p>\n<p><strong>Livelocks</strong> occur when we try to avoid deadlocks using asynchronous locking, where multiple threads competing for the same set lock(s), avoid acquiring the lock(s) to allow other threads to go with the lock first, and eventually never get to acquire a lock and proceed; causing starvation. See below to understand how a <strong><em>aysnc-locking which is a strategy to avoid Deadlock can be the reason for a Livelock</em></strong></p>\n<p>Here are some of the <strong>theoretical solutions</strong> to <strong>Deadlocks</strong>, and one them (second one) is the main <strong>reason</strong> for <strong>Livelocks</strong></p>\n<h3>Theoretical Approaches</h3>\n<h4>Don’t use locks</h4>\n<p>Not possible where two operations need to be synchronized, example, a simple bank transfer, where you debit one account before you can credit the other account, and not let any other thread touch the balance in accounts until the current thread is done.</p>\n<h4>Don’t block on locks, if a thread can’t acquire a lock, it should release previously acquired locks to try again later</h4>\n<p>Cumbersome to implement and can cause starvation (<strong>Livelocks</strong>) where a thread is always letting locks go only to try again and do the same. Also, this approach may have over-heads in frequent thread context switching reducing a system’s overall performance. Also, there is no way for CPU scheduler implement fairness as it doesn’t know which thread has been actually waiting for lock(s) the longest.</p>\n<h4>Let threads always request locks in a strict ordering</h4>\n<p>Easier said than done, for example. If we are writing a function to transfer money from Account A to B, we can write something like</p>\n<pre><code>// at compile time, we take lock on first arg then second  \npublic void transfer(Account A, Account B, long money) {  \n  synchronized (A) {  \n    synchronized (B) {  \n      A.add(amount);  \n      B.subtract(amount);  \n    }  \n  }  \n}\n\n// at runtime we cannot track how our methods will be called  \npublic void run() {  \n  new Thread(()->this.transfer(X,Y, 10000)).start();  \n  new Thread(()->this.transfer(Y,X, 10000)).start();  \n}  \n// this run() will create a deadlock  \n// first thread locks on X, waits for Y  \n// second thread locks on Y, waits for X\n</code></pre>\n<h3>Real world solution</h3>\n<p>We can combine approaches of lock ordering and timed locks to arrive at a real word solution</p>\n<h4>Business Determined Lock Ordering</h4>\n<p>We can improve our approach by discriminating between A and B based on whose account number is greater or smaller.</p>\n<pre><code>// at run time, we take lock on account with smaller id first  \npublic void transfer(Account A, Account B, long money) {  \n  final Account first = A.id &#x3C; B.id ? A : B;  \n  final Account second = first == A? B: A;\n\n  synchronized (first) {  \n    synchronized (second) {  \n      first.add(amount);  \n      second.subtract(amount);  \n    }  \n  }  \n}\n\n// at runtime we cannot track how our methods will be called  \npublic void run() {  \n  new Thread(()->this.transfer(X,Y, 10000)).start();  \n  new Thread(()->this.transfer(Y,X, 10000)).start();  \n}\n</code></pre>\n<p>For example, if <code>X.id = 1111</code>, and <code>Y.id = 2222</code>, since we take in first account as the one with smaller account id, the locking order for executions of <code>transfer(Y, X, 10000)</code> and <code>transfer(X,Y, 10000)</code> will be same. X having an account number lesser than Y, both threads will attempt to lock X before Y and only one of them will succeed and proceed to lock Y finish and release locks on X and Y before the other threads acquires locks and can proceed.</p>\n<h4>Business Determined Timed Wait tryLock / async Locking Requests</h4>\n<p>The solution of using a business determined lock ordering works only if for associative relationships where a logic at one place transfer(….), such as in our method determines how resources are coordinated.</p>\n<p>We may end up have other methods / logic, which ends up using an ordering logic that is incompatible with <code>transfer(…)</code>. To avoid <strong>Deadlock</strong> in such cases, it is advisable to use async locking, where we try to lock a resource for finite / realistic time (max transaction time) + Small-random-wait-time so all threads don’t try ti re-acquire too early and not all at the same time respectively hence avoiding <strong>Livelocks</strong> (starvation due to unviable attempts at acquiring locks)</p>\n<pre><code>// assume Account#getLock() gives us account's Lock  (java.util.concurrent.locks.Lock)  \n// Account could encapsulate lock, provide lock() /unlock()\n\npublic long getWait() {   \n/// returns moving average of transfer times for last n transfers + small-random-salt in millis so all threads waiting to lock do not wake up at the same time.  \n}\n\npublic void transfer(Lock lockF, Lock lockS, int amount) {  \n  final Account first = A.id &#x3C; B.id ? A : B;  \n  final Account second = first == A? B: A;  \n  final Lock lockF = first.getLock();  \n  final Lock lockS = second.getLock();\n\n  boolean done = false;\n\n  do {  \n    try {  \n      try {  \n        if (lockF.tryLock(getWait(), _MILLISECONDS_)) {  \n          try {  \n            if (lockS.tryLock(getWait(), _MILLISECONDS_)) {  \n              done = true;  \n            }  \n          } finally {  \n            lockS.unlock();  \n          }  \n        }  \n      } catch (InterruptedException e) {  \n        throw new RuntimeException(\"Cancelled\");  \n      }  \n    } finally {  \n      lockF.unlock();  \n    }  \n  } while (!done);  \n\n}\n\n// at runtime we cannot track how our methods will be called  \npublic void run() {  \n    new Thread(()->this.transfer(X,Y, 10000)).start();  \n    new Thread(()->this.transfer(Y,X, 10000)).start();  \n}\n</code></pre>\n<hr>\n<p>This article is also aviable on my <a href=\"https://medium.com/simars/deadlocks-how-to-avoid-them-in-real-world-con-3fa8662ed6d4?source=friends_link&#x26;sk=8755ba0f19a9cfd9ed5e709a4c5b9d2d\"><strong>Medium</strong></a> publication. If you like the artile, or have any comments and suggestions, please <strong><em>clap</em></strong> or leave <strong><em>comments</em></strong> on <a href=\"https://medium.com/simars/deadlocks-how-to-avoid-them-in-real-world-con-3fa8662ed6d4?source=friends_link&#x26;sk=8755ba0f19a9cfd9ed5e709a4c5b9d2d\"><strong>Medium</strong></a>.</p>","frontmatter":{"path":"/deadlocks-how-to-avoid-them-in-real-world-concurrency","title":"Deadlocks & Livelocks — How to avoid in real world Concurrency?","author":"Simar Paul Singh","date":"2019-04-10"}}},"pathContext":{}}

